@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar{z-index:1;background-color:#d47b4a;display:flex;justify-content:space-evenly;align-items:center}.navBar .toggle{flex:1 1;display:flex;justify-content:center;align-items:center}.navBar .toggle .toggleImg{height:50px;-webkit-filter:drop-shadow(2px 2px 2px #3a2e27);filter:drop-shadow(2px 2px 2px #3a2e27)}.navBar .toggle .clickMe{height:40px;cursor:default}.navBar .logo{flex:1 1;display:flex;justify-content:center;align-items:center}.navBar .navLinks{flex:1 1;display:flex;justify-content:space-around}.navBar .navLinks .navLink{display:flex;justify-content:center;align-items:center;font-size:18px;font-family:"Source Code Pro", monospace;font-weight:600;color:#fff}.navBar .navLinks .navLink a{color:#fff;text-decoration:none}.navBar .navLinks .navLink p{cursor:pointer}.navBar img{cursor:pointer;height:80px}

.aboutMe{display:flex}.aboutMe .aboutMeIconSection{display:flex;width:50%;align-items:center;justify-content:space-evenly}.aboutMe .aboutMeIconSection .aboutMeIcon img{height:60px}.aboutMe .aboutMeTitle{display:flex;flex-direction:column;justify-content:center;align-items:center}.aboutMe .aboutMeTitle1{font-size:48px}.aboutMe .aboutMeTitle2{font-size:60px}.aboutMe .aboutMeQASection{display:flex;flex-direction:column;width:70%}.aboutMe .aboutMeQASection .aboutMeQA{display:flex;flex-direction:column;font-size:24px;margin-bottom:20px}.aboutMe .aboutMeQASection .aboutMeQA .aboutMeQ{font-weight:400}.aboutMe .aboutMeQASection .aboutMeQA .aboutMeA{font-weight:600}.aboutMe .aboutMeTextDay{flex:1 1;display:flex;flex-direction:column;align-items:center;margin-top:40px;margin-bottom:40px;justify-content:space-evenly;font-size:48px;font-family:"Source Code Pro", monospace;font-weight:600;color:#683e30}.aboutMe .aboutMeTextNight{flex:1 1;display:flex;flex-direction:column;justify-content:space-evenly;margin-top:40px;margin-bottom:40px;align-items:center;font-size:48px;font-family:"Source Code Pro", monospace;font-weight:600;color:#fff5e3}.aboutMe .aboutMePhoto{flex:1 1;background:#9d6e5e;display:flex;justify-content:center;align-items:center}.aboutMe .aboutMePhoto img{height:92vh}

.projectGalleryDay{background:#ffd9c7;height:92vh}.projectGalleryNight{background:#684132;height:92vh}.projectSection{display:flex;flex-flow:row wrap;width:60%;margin:auto;height:80vh;justify-content:space-evenly;align-items:space-evenly}.titleSection{padding-top:30px;padding-bottom:30px;font-family:"Source Code Pro", monospace;font-weight:600;font-size:52px;text-align:center}.titleSection .titleNight{color:#fff5e3}.titleSection .titleDay{color:#683e30}.projectElement{position:relative}.overlayedImage{display:block;height:300px;width:300px;object-fit:cover}.overlay{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;transition:all 0.1s ease-in;background-color:rgba(104,62,48,0.8);display:flex;flex-direction:column;justify-content:center;align-items:center}.overlay img{height:70px;opacity:1}.overlay p{color:white;font-family:"Source Code Pro", monospace;font-weight:700;font-size:20px}.text{color:white;font-size:20px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center}

.skills{height:92vh}

.contactNight{background:#684132;display:flex;flex-direction:column;align-items:space-evenly;justify-content:center;width:100%;height:88.5vh}.contactDay{background:#ffd9c7;display:flex;flex-direction:column;align-items:space-evenly;justify-content:center;width:100%;height:88.5vh}.contactImage{display:flex;justify-content:center}.contactImage img{height:500px}.contactIconSection{display:flex;width:25%;align-items:center;justify-content:space-evenly;margin:auto}.contactIconSection .contactIcon img{height:60px}

.footer{width:100%;background:#d5a291;display:flex;justify-content:center;align-items:center;height:36px}.copyright{font-size:16px;font-family:"Source Code Pro", monospace;font-weight:700;color:#fff}

.homeNight{background:#3a2e27}.homeDay{background:#fff5e3}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

